.container{
    height: 100vh;
    display:flex ;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

video{
    position: absolute;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: translate (-50% -50%);
    z-index: -1;
    filter:brightness(70%) ;
}

.title{
    font-size: 75px;
    color: #fff;
    text-shadow: 2px 2px 8px black;
}

@media (max-width: 620px)
{
    .title{
        font-size: 65px;
        
        margin-left: 10%;
    }
}

@media (max-width: 480px)
{
    .title{
        font-size: 60px;
        
        margin-left: 10%;
    }
}

@media (max-width: 445px)
{
    .title{
        font-size: 55px;
        
        margin-left: 10%;
    }
}

@media (max-width: 410px)
{
    .title{
        font-size: 50px;
        margin-left: 10%;
    }
}


.containerInput{
    margin: 35px 0;
    padding: 15px;
    display: flex;
    background-color: transparent;
    border-radius: 8px;
    background-color: rgba(247, 247, 247, 0.2);
}
.containerInput input{
    background-color: transparent;
    border: 0;
    font-size: 20px;
    color: #fff;
    outline: none;
    margin-right: 8px;
    text-shadow: 2px 2px 8px black;
}
.containerInput input::placeholder{
    color: #fff;
}

.buttonSeacrch{
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.2s;

}

.buttonSeacrch:hover{
    transform: scale(1.2);
}

.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: rgba(240, 240, 240, .2);
    width: 80vw;
    max-width: 500px;
    min-width: 360px;
    height: 200px;
    border-radius: 8px;
}



.main h2{
    margin: 16px 0;
    font-size: 39px;
    color: white;
    text-shadow: 2px 2px 8px black;
}

.main span{
    margin-bottom: 16px;
    font-weight: bold;
    color: white;
    text-shadow: 2px 2px 8px black;
}

footer{
    position: absolute;
    margin-top: 95vh;
    display: flex;
    justify-content: center;
    align-items: center;
}
footer h2{
    margin-bottom: 5px;
    font-size: 12px;
    color: white;
    text-shadow: 2px 2px 8px black;
}
footer a{
    margin-left: 7px;
    color: white;
    text-shadow: 2px 2px 8px black;
}